import React, { useState } from 'react';
import { useNavigate } from 'react-router';
import styled from 'styled-components';

const Nav = () => {
  const [inputStatus, setInputStatus] = useState(''); //     state
  const navigate = useNavigate(); //    

  //   
  const handleColor = id => {
    setInputStatus(id); //inputStatus  id 
    navigate(`/${id}`); //   id  
  };

  return (
    <Wrapper>
      {/*  */}
      <LogoBox></LogoBox>
      {/*  */}
      <>
        {/* changeColor      true    */}
        <LNB_MeNU_A
          id="project"
          onClick={() => handleColor('project')}
          changeColor={inputStatus === 'project'}
        >
          <Menu_Name>Project List</Menu_Name>
        </LNB_MeNU_A>
        <LNB_MeNU_B
          id="chart"
          onClick={() => handleColor('chart')}
          changeColor={inputStatus === 'chart'}
        >
          <Menu_Name>Chart</Menu_Name>
        </LNB_MeNU_B>
      </>
    </Wrapper>
  );
};

const Wrapper = styled.nav`
  position: fixed;
  width: 265px;
  height: 100%;
  left: 0;
  background-color: #202020;
`;

const LogoBox = styled.div`
  width: 100%;
  height: 60px;
  background-color: ${({ theme }) => theme.blue};
`;

const ImgBox = styled.div`
  width: 96.5px;
  height: 100%;
  margin-left: 15px;
`;

const Category = styled.div`
  width: 100%;
  height: 40px;
  margin: 20px 0;
  padding: 10px 20px 10px 30px;
  cursor: pointer;
`;

const LNB_MeNU_A = styled(Category)`
  background-color: ${change =>
    change.changeColor ? 'rgba(44, 120, 255, 0.25)' : 'inherit'};
`;

const LNB_MeNU_B = styled(Category)`
  background-color: ${change =>
    change.changeColor ? 'rgba(44, 120, 255, 0.25)' : 'inherit'};
`;

const Menu_Name = styled.div`
  color: ${({ theme }) => theme.white};
  font-family: ${({ theme }) => theme.font};
  font-size: 16px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.25;
  letter-spacing: normal;
  text-align: left;
`;

export default Nav;
