import React, { useState, useEffect, useCallback } from 'react';
import styled from 'styled-components';
import Title from '../components/Title';
import { TwoButton } from '../components/Button';
import BasicChart from '../components/BasicChart';
import Highcharts from 'highcharts';

const Chart = () => {
  const [upload, setUpload] = useState(false); //json    true  state
  const [data, setData] = useState([]); //Chart.json   state

  //Chart.json  
  useEffect(() => {
    fetch('data/Chart.json')
      .then(res => res.json())
      .then(data => {
        setData(data.res);
      });
  }, []);

  // 

  useEffect(() => {
    async function chartRequest() {
      if (data && data.length > 0) {
        const chart = await Highcharts.chart('request', {
          chart: {
            type: 'line',
          },
          title: {
            text: '',
          },
          yAxis: {
            title: '',
          },
          yAxis: {
            title: {
              text: '',
            },
          },
          credits: {
            enabled: false,
          },
          series: [
            {
              name: '',
              data: data[0].data,
            },
          ],
        });
      }
    }
    chartRequest();
  }, [upload]);

  // 

  useEffect(() => {
    async function chartTotal() {
      if (data && data.length > 0) {
        const chart = await Highcharts.chart('request_total', {
          chart: {
            type: 'line',
          },
          title: {
            text: '',
          },
          yAxis: {
            title: '',
          },
          yAxis: {
            title: {
              text: '',
            },
          },
          credits: {
            enabled: false,
          },
          series: [
            {
              name: '',
              data: data[1].data,
            },
          ],
        });
      }
    }
    chartTotal();
  }, [upload]);

  //import    
  //  json     
  const handleCheck = e => {
    const values = e.target.value.includes('chart.json');

    if (values) setUpload(!upload);
    else alert('Unknown json format.');
  };

  return (
    <Main>
      <Title titleText="Chart" />
      <TwoButton buttonTextFirst="Import" handleCheck={handleCheck} />
      {upload && (
        <BasicChart
          data={data}
          request="request"
          request_total="request_total"
        />
      )}
    </Main>
  );
};

const Main = styled.main`
  margin-left: 18%;
`;
export default Chart;
