import React, { useState, useEffect, useRef, useCallback } from 'react';
import styled from 'styled-components';
import Title from '../components/Title';
import { TwoButton } from '../components/Button';
import Import from '../components/Import';
import swal from 'sweetalert';
import '../components/Alert.css';

const ProjectList = () => {
  // 
  const now = new Date();
  const year = now.getFullYear();
  const month = now.getMonth();
  const day = now.getDate();
  const hours = now.getHours();
  const min = now.getMinutes();
  let today = `${year}.${month}.${day} ${hours}:${min}`;

  const [upload, setUpload] = useState(false); //json    true  state
  const [data, setData] = useState([]); //Project_list.json input   state
  const [inputData, setInputData] = useState([]); //alert input    state
  const [check, setCheck] = useState(false); //handleAlert   true  state
  //import button   alert input   state
  const [inputs, setInputs] = useState({
    service_name: '',
  });

  const { service_name } = inputs;

  const nextId = useRef(0);

  //Project_list.json  
  useEffect(() => {
    fetch('data/Project_list.json')
      .then(res => res.json())
      .then(data => {
        setData(data.res);
      });
  }, []);

  //import    
  //  json    no data   
  const handleCheck = useCallback(e => {
    const values = e.target.value.includes('project_list.json');

    if (!values) alert('Unknown json format.');
    else setUpload(upload);

    if (values) setUpload(!upload);
  }, []);

  // 
  useEffect(() => {
    if (check && inputs) {
      //user   
      const user = {
        id: nextId.current,
        service_name,
        creation_date: today,
      };
      //inputData  user 
      setInputData([...inputData, user]);
      nextId.current += 1; //id +1 
    }
  }, [check]);

  //import button   , alert  
  const handleAlert = () => {
    swal({
      title: 'Service name',
      content: 'input',
      buttons: ['Cancel', 'Add'],
    }).then(result => {
      const lower = result.toLowerCase();
      const num = result >= 0;

      //    
      if (result === lower || num) {
        //inputs   
        //check true 
        setInputs({
          service_name: result,
        });
        setCheck(!check);
        setCheck(true);
      } else {
        swal({
          title: 'Service name',
          text: 'only lowercase letters and numbers allowed.',
          buttons: 'confirm',
        });
      }
    });
  };

  //   , onDownloadBtn  
  const downloadFile = async ({ data, fileName, fileType }) => {
    //   Blob  [Blob? (Binary Large Object, ) , ,       ]
    const blob = new Blob([data], { type: fileType });
    // a      
    //  
    const link = document.createElement('a');
    link.download = fileName;
    link.href = await URL.createObjectURL(blob);

    const clickEvt = new MouseEvent('click', {
      view: window,
      bubbles: true,
      cancelable: true,
    });
    link.dispatchEvent(clickEvt);
    link.remove();
  };

  //   
  // data, fileName, fileType   downloadFile  
  const onDownloadBtn = () => {
    const name = 'project_list'; //
    downloadFile({
      data: JSON.stringify(data),
      fileName: `${name}.json`,
      fileType: 'text/json',
    });
  };

  // 
  const onRemove = id => {
    setInputData(inputData.filter(del => del.id !== id));
  };

  return (
    <Main>
      <Title titleText="Project list" />
      <TwoButton
        buttonTextFirst="Import"
        buttonTextSecond="Export"
        handleCheck={handleCheck}
        data={data}
        onDownloadBtn={onDownloadBtn}
      />
      {!upload && <EmptyData>Have no data</EmptyData>}
      {upload && (
        <Import
          className="import"
          data={data}
          handleAlert={handleAlert}
          inputData={inputData}
          onRemove={onRemove}
        />
      )}
    </Main>
  );
};

const Main = styled.main`
  margin-left: 18%;
`;

const EmptyData = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 951px;
  height: 90px;
  margin: 40px 75px 810px 0;
  font-size: 18px;
  letter-spacing: normal;
  color: ${({ theme }) => theme.black};
  font-family: ${({ theme }) => theme.font};
`;
export default ProjectList;
